import { useForm } from "react-hook-form";
import { cn } from "../../utils/custom_functions";
import { useState } from "react";
import { Button } from "../reusable_components/custom_button";
import { useMutation } from "react-query";
import { requestPasswordResetFunc } from "../../api/api";
import { ThreeDots } from "react-loader-spinner";
import { ErrorAlert, SuccessAlert } from "../reusable_components/alerts/alerts";

export default function RequestPasswordReset() {
  const [disableUserInterface, setDisableUserInterface] = useState(false);
  const {
    register,
    handleSubmit,
    formState: { isLoading: isFormLoading, errors, isSubmitting },
  } = useForm();

  const {
    isLoading: isRequestPasswordResetLoading,
    isSuccess: isRequestPasswordResetSuccess,
    mutateAsync: RequestPasswordResetAsync,
    isError: isRequestPasswordResetError,
    error: requestPasswordResetError,
  } = useMutation({
    mutationKey: "request-password-reset",
    mutationFn: requestPasswordResetFunc,
    onSuccess: (_) => {
      setDisableUserInterface((r) => false);
    },
    onError: (_) => {
      setDisableUserInterface((r) => false);
    },
    onMutate: (_) => {
      setDisableUserInterface((r) => true);
    },
  });

  const onSubmit = async (data) => {
    try {
      await RequestPasswordResetAsync(data);
    } catch (error) {}
    console.log(data);
  };

  return (
    <main className="max-w-xl mx-auto p-6 my-6 text-black bg-white">
      <div className="rounded-lg p-6 shadow-sm border border-gray-200">
        <h2 className="text-start text-black pb-4 px-2 font-extrabold text-3xl">
          Request password reset
          <p className="text-sm font-normal text-slate-600 pt-2">
            Thank you for trusting Matols Logistic Services with your personal
            information.
          </p>
        </h2>

        <form onSubmit={handleSubmit(onSubmit)}>
          <div>
            <input
              {...register("email", {
                required: "Email address required",
                disabled: disableUserInterface,
              })}
              type="email"
              name="email"
              placeholder="matols@gmail.com"
              className={cn(
                "text-sm text-slate-800  w-full relative shadow-sm block rounded-md border-gray-200 focus:z-10 focus:ring-yellow-400 focus:border-yellow-400 disabled:opacity-50 disabled:pointer-events-none",
                {
                  "text-red-500 placeholder:text-red-500": errors.email,
                }
              )}
            />
            <span className="text-xs text-gray-500">
              Enter your emaill address
            </span>
          </div>
          <div className="pt-4">
            <div>
              {errors.email && (
                <p className="pt-2 text-red-500 text-sm italic">
                  {errors.email.message}
                </p>
              )}
            </div>
            {isRequestPasswordResetError && (
              <ErrorAlert
                className="mb-4"
                secondaryText={`${requestPasswordResetError.response.data.message} ${requestPasswordResetError.message}`}
              />
            )}
            {isRequestPasswordResetSuccess && (
              <SuccessAlert
                secondaryText={
                  "Your password reset link has been send to this email"
                }
                className="mb-4"
              />
            )}
            <Button disabled={disableUserInterface}>
              {isRequestPasswordResetLoading ? (
                <div className="w-full h-full flex justify-center items-center">
                  <ThreeDots visible width="40" height="20" color="#000" />
                </div>
              ) : (
                " Request password reset"
              )}
            </Button>
          </div>
        </form>
      </div>
    </main>
  );
}
